let that;
export default {
	data() {
		return {
			isok: false,
			selectext: '',
			goodStatusList: ['全部', '已上架', '已下架'],
			goodsState: '',
			isSelect: false,
			putUp: 'color:#0E5E56;border:1px solid #0E5E56',
			putDown: 'color: #D92B2B;border:1px solid #D92B2B',
			modelList: [{ name: '{模板可编辑}木质骨灰纪念盒子', price: '199', num: 1, putActive: false }],
			storeId: '',
			goodsInfo: ''
		};
	},
	onLoad() {
		that = this;
		this.init();
	},
	methods: {
		init() {
			this.selectAllGoods();
			this.$api.showLoading();
		},
		selectAllGoods() {
			this.storeId = uni.getStorageSync('storeId');
			let storeId = this.storeId;
			console.log(this.storeId);
			this.$api
				.selectGoodsByStoreAndState({
					storeId: storeId,
					goodsState: this.goodsState
				})
				.then(data => {
					console.log(data);
					if ((data.data.code = 100000)) {
						this.goodsInfo = data.data.goodsInfo;
						this.isok = true;
						uni.hideLoading();
					}
				});
		},
		selectService(e) {
			if (e) {
				this.selectext = e;
				setTimeout(() => {
					this.isSelect = false;
				}, 100);
				//  重新
				if (e == '已上架') {
					this.goodsState = 1;
				} else if (e == '已下架') {
					this.goodsState = 2;
				} else if (e == '全部') {
					this.goodsState = '';
				}
				this.selectAllGoods();
			}
		},
		addGoods() {
			uni.navigateTo({
				url: '../addGoods/addGoods'
			});
		},
		//  上架商品
		putService(e) {
			this.lowShelfGoods(e)
			if (e.goodsState==2) {
				uni.showModal({
					content: '确定要上架此商品吗',
					success: re => {
						if (re.confirm) {
							this.$api
								.upperShelfGoods({
									goodsId: e.goodsId
								})
								.then(data => {
									console.log(data);
									if ((data.data.code = 100000)) {
										this.$api.showToast('上架成功');
										this.selectAllGoods()
									}
								});
						}
					}
				});
			}
			console.log(e);
			e.putActive = !e.putActive;
		},
		// 下架商品
		lowShelfGoods(e){
			if(e.goodsState==1){
				uni.showModal({
					content: '确定要下架此商品吗',
					success: re => {
						if (re.confirm) {
							this.$api
								.lowerShelfGoods({
									goodsId: e.goodsId
								})
								.then(data => {
									console.log(data);
									if ((data.data.code = 100000)) {
										this.$api.showToast('下架成功');
										this.selectAllGoods()
									}
								});
						}
					}
				});
			}
		},
		btnColor(e) {
			console.log(e);
		},
		deleteService(e) {
			// 删除模板
			uni.showModal({
				content: '确定要删除此商品吗',
				success: re => {
					if (re.confirm) {
						this.$api
							.deleteGoods({
								goodsId: e
							})
							.then(data => {
								if (data.data.code == 100000) {
									this.$api.showToast('删除成功');
									this.selectAllGoods();
								}
							});
					}
				}
			});
		},
		// 编辑商品
		editor(e) {
			console.log(e)
			//  每次编辑自动更新 需要修改的 商品
			uni.setStorageSync('editgoods',e)
			if (e) {
				uni.navigateTo({
					url: '../addGoods/addGoods?type=update',
				});
			}
		}
	}
};
